import React from 'react';
import logo from './logo.svg';
import './App.css';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Alert from '@mui/material/Alert';

function App() {
  const [isTrusted, setIsTrusted] = React.useState('unknown');
  const handleClick = (e) => {
    if (e.isTrusted) {
      setIsTrusted('trusted');
    } else {
      setIsTrusted('untrusted');
    }
  }

  const handleSimulateClick = (e) => {
    const button = document.getElementById('test');
    if (button) {
      button.click();
    }
  }

  return (
    <div >
      <Box sx={{ flexGrow: 1 }}>
        <AppBar position="static">
          <Toolbar>
            <IconButton
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              sx={{ mr: 2 }}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
              Captcha Test
            </Typography>
          </Toolbar>
        </AppBar>
      </Box>
      <div style={{
        width: '100%',
        height: 'calc(100vh - 80px)',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
      }}>
        <Button color="inherit" onClick={handleClick} id='test'>I'm a hacker</Button>
        <Button onClick={handleSimulateClick} color='primary'>Simulate click</Button>
        {isTrusted === 'untrusted' && (<Alert severity="error">Sorry dude, you are not a hacker</Alert>)}
        {isTrusted === 'trusted' && (<Alert severity="success">Yes, you got it!</Alert>)}
      </div>
    </div>
  );
}

export default App;
